.App {
	color: #333;
	margin: 0 auto;
	text-align: center;
	width: 50%;
}

.rollBtn {
	height: 300px;
	width: 300px;
}

.clickable {
	cursor: pointer;
}

.old-rolls {
	list-style: none;
	font-size: 48px;
	line-height: 100px;
	padding-left: 0;
	margin: 0;
	height: 75vh;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: flex-start;
	align-content: flex-start;
}

.dice, .rolls {
	width: 50%;
}

.old-rolls li, .old-rolls img {
	height: 100px;
	width: 100px;
}

.wrap {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: flex-start;
	align-content: stretch;
}